import React, { useState } from "react";
import { DragDropContext, DropResult } from "react-beautiful-dnd";
import { useRecoilState } from "recoil";
import styled from "styled-components";
import { toDoState } from "./atoms";
import Board from "./Components/Board";

const Wrapper = styled.div`
  display: flex;
  width: 100vw;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: ${(props) => props.theme.bgColor};
`;

const Boards = styled.div`
  display: flex;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  gap: 10px;
`;

const App = () => {
  const [toDos, setTodos] = useRecoilState(toDoState);
  const onDragEnd = (info: DropResult) => {
    const { destination, draggableId, source } = info;
    //        => destination : null => boolean  false
    if (!destination) {
      console.log("no move");
      return;
    }
    //      id ? => same board movement
    if (destination?.droppableId === source.droppableId) {
      setTodos((allBoards) => {
        //   id       
        const boardCopy = [...allBoards[source.droppableId]];
        const taskObj = boardCopy[source.index]
        //    task 
        boardCopy.splice(source.index, 1);
        //    draggable Id  task 
        boardCopy.splice(destination?.index, 0, taskObj);
        console.log(allBoards);
        //    toDoState 
        return {
          ...allBoards,
          [source.droppableId]: boardCopy,
        };
      });
    }

    //       => cross movement
    if (destination.droppableId !== source.droppableId) {
      /* 
         : draggableId   string .
           toDoSate IToDoStata
      
          export interface IToDo {
            id:number;
            text:string;
          }
          interface IToDoSate {
            [key:string]: IToDo[]
          }
              
          

          . "Doing": [{text:"blabla" id: "1"},{text:"blabla" id: "2"}]
            draggableId  "1"   {text:"blabla" id: "1"}   
      */
      
      setTodos((allBaords) => {
        console.log("Im here");
        const sourceBoard = [...allBaords[source.droppableId]];
        const taskObj = sourceBoard[source.index]
        const destinationBoard = [...allBaords[destination.droppableId]];
        sourceBoard.splice(source.index, 1);
        destinationBoard.splice(destination.index, 0, taskObj);
        return {
          ...allBaords,
          [source.droppableId]: sourceBoard,
          [destination.droppableId]: destinationBoard,
        };
      });
    }
  };

  return (
    <DragDropContext onDragEnd={onDragEnd}>
      <Wrapper>
        <Boards>
          {Object.keys(toDos).map((boardId) => (
            <Board
              key={boardId}
              boardId={boardId}
              toDos={toDos[boardId]}
            ></Board>
          ))}
        </Boards>
      </Wrapper>
    </DragDropContext>
  );
};

export default App;
